}

.progress-marker {
  display: block;
  position: relative;
}

.progress-marker::before {
  content: attr(data-text);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 20;
  width: 24px;
  height: 24px;
  padding-bottom: 2px;
  border-radius: 50%;
  transition: background-color, border-color;
  transition-duration: 0.3s;
}

.progress-marker::after {
  content: "";
  display: block;
  position: absolute;
  z-index: -10;
  top: 10px;
  right: -12px;
  width: 100%;
  height: 4px;
  transition: background-color 0.3s, background-position 0.3s;
}

.progress-text {
  display: block;
  padding: 8px 8px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.progress-title {
  margin-top: 0;
}

.progress-step .progress-marker {
